const BioSection: React.FC = () => {
  return (
    <>
      <Typography variant="h4">About Me</Typography>
      <Paper elevation={3} sx={{ padding: 2, marginBottom: 2 }}>
        <Typography paragraph>{Bio.description}</Typography>
        <Typography variant="h5">What I'm Doing</Typography>
        <Grid container>
          {whatImDoing.map((e) => (
            <Grid key={e.do} xs={12} md={6} lg={6}>
              <Box padding={2}>
                <Paper
                  style={{
                    padding: "20px",
                  }}
                >
                  <Box
                    gap={2}
                    display="flex"
                    justifyContent="space-around"
                    alignItems="center"
                  >
                    {e.img}
                    <Box>{e.do}</Box>
                  </Box>
                </Paper>
              </Box>
            </Grid>
          ))}
        </Grid>
      </Paper>
    </>
  );
};

export default BioSection;
